.NewGenmai-form {
  height: calc(100vh - 175px);
  width: calc(100vh - 175px);
}

.NewGenmai-form form {
  width: 100%;
  height: 100%;
  margin-bottom: 25px;

  display: flex;
  flex-direction: column;
}

.NewGenmai-form-field {
  margin-bottom: 25px;
  flex: 0;
}

.NewGenmai-form-field-text {
  flex: 1;
}

.NewGenmai-form-field label {
  font-weight: bold;
}

.NewGenmai-form-field input,
.NewGenmai-form-field textarea {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.NewGenmai-form-field textarea {
  height: 100%;
}

.NewGenmai-form-buttons input {
  padding: 15px;
  background: #66f;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.NewGenmai-form-buttons a {
  display: inline-block;
  padding: 15px;
  text-decoration: none;
}

.NewGenmai-form-buttons a:hover {
  text-decoration: underline;
}

.HakumaiJson textarea {
  height: calc(100vh - 175px);
  width: calc(100vh - 175px);
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.jsonbtn {
  margin-left: calc(100vw - 30vw);
  margin-top: 100px;
}
