body {
  font-family: 'Avenir Next';
}

#root,
.App {
  height: 100%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.app-intro {
  padding-top: 50px;
}

.app-content {
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 50px;
  padding-left: 50px;
}

@media screen and (max-width: 1065px) {
  .app-content {
    margin: 0 20px;
  }
}
