body,
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  line-height: 1.4;
}

hr {
  height: 1px;
  border: none;
  border-top: 1px solid #ddd;
  margin: 30px 0;
}

a {
  color: #33f;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.btn {
  display: inline-block;
  padding: 5px 10px;
  margin-left: 5px;
  background: #ddd;
  border-radius: 5px;
  text-decoration: none;
  color: #333;
  border: none;
}

.btn:hover {
  background: #eaeaea;
  cursor: pointer;
}
