.main {
  padding: 100px 80px;
}

.section-title {
  border-bottom: 2px solid #dee7ec;
  padding-bottom: 15px;
  margin-bottom: 50px;
  letter-spacing: 15px;
}

.copy-container span,
.contents-item p span {
  color: #ff4a4a;
}

.copy-container h6 {
  opacity: 0.7;
}

.contents-item {
  display: inline-flex;
  width: 300px;
  margin: 50px;
}

.contents-item p {
  margin-left: 30px;
}

.contents-item img {
  width: 100px;
  height: 100px;
}

.contents-cards:hover {
  text-decoration: none;
  border-right: 2px solid #dee7ec;
  border-bottom: 2px solid #dee7ec;
}
