.list {
  margin-bottom: 100px;
}

.list-item {
  display: flex;
  align-items: center;

  padding: 20px 10px;
  border-bottom: 1px solid #dadada;
}

.list-item a {
  display: inline-block;
  width: 100%;
  color: #33f;
  text-decoration: none;
}
.list-item a:hover {
  text-decoration: underline;
}
.list-item h2 {
  padding: 0;
  margin: 0;
}

.list-date {
  text-align: right;
  flex-grow: 1;
  color: #cfcfcf;
  font-size: 0.9em;
}

@media screen and (max-width: 750px) {
  .list-item {
    display: block;
  }
  .list-item h2 {
    width: 100%;
    margin-bottom: 10px;
  }
}
