.footer {
  margin-top: 20px;
  background-color: #2f5167;
  color: #fff;
  height: 30px;
  padding: 40px;
}

.footer-logo {
  font-size: 32px;
  float: left;
}

.footer-logo p {
  margin-top: 0px;
  margin-right: 20px;
  float: left;
}

.footer-logo img {
  height: 45px;
  float: left;
}

.footer-list {
  float: right;
}

.footer-list li {
  float: right;
  padding-right: 10px;
  margin-right: 10px;
  color: #fff;
  list-style: none;
}

.footer-list li:hover {
  text-decoration: none;
  border-right: 1px dotted #dee7ec;
  border-bottom: 1px dotted #dee7ec;
}

.footer-list .twitter {
  color: #55acee;
  font-size: 24px;
}

.logo {
  float: left;
  padding-right: 20px;

  width: 20px;
  height: 20px;
}
