.navbar {
  background-color: #26d0c9;
  color: #fff;
  height: 90px;

  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.logo {
  float: left;
  padding-right: 20px;

  width: 20px;
  height: 20px;
}

.logo img {
  height: 45px;
}

.navbar h1 {
  margin: 0;
  padding: 20px;
}

.navbar-buttons {
  text-align: right;
  flex-grow: 1;
  margin-right: 20px;
}

.navbar-buttons .btn {
  padding: 10px 15px;
}

.navbar-buttons .btn:hover {
  border: 2px solid #dee7ec;
  text-decoration: none;
}

.navbar-links {
  color: #fff;
  padding-left: 20px;
}

.navbar-links:hover {
  text-decoration: none;
  border-right: 2px solid #dee7ec;
  border-bottom: 2px solid #dee7ec;
}
